@value makerBlack, makerWhite, makerColorful, makerShadow from from '../../common/colors.css';

.card {
  display: flex;
  margin: 24px 16px;
  border-radius: 9px;
  width: 87mm;
  height: 52mm;
  padding: 16px;
  box-shadow: makerShadow;
}

.cardInfo {
  margin-left: 12px;
}

img {
  width: 37%;
  height: 57%;
  object-fit: contain;
}

.name,
.company,
.job,
.email,
.message {
}

.name {
  font-size: 17px;
  padding-bottom: 0.4em;
}

.company {
  font-size: 13px;
  padding-bottom: 0.6em;
}

.job {
  font-size: 13px;
  padding-bottom: 3.5em;
}

.email,
.message {
  font-size: 13px;
  padding-bottom: 0.5em;
}

.card.dark {
  background-color: makerBlack;
  color: makerWhite;
}

.card.light {
  background-color: makerWhite;
  color: makerBlack;
}

.card.colorful {
  background: makerColorful;
}
