.form {
  display: grid;
  /* grid-template-columns: 1fr 1fr 1fr; */
  grid-template-areas:
    "name name company company color color"
    "job job phoneNumber phoneNumber email email"
    "message message message message message message"
    "file file file delete delete delete";
  margin: 16px;
  border: 0;
  border-left: 1px solid grey;
  border-top: 1px solid grey;
}

.name,
.company,
.color,
.job,
.email,
.message,
.file,
.delete,
.phoneNumber {
  border: 0;
  border-bottom: 1px solid grey;
  border-right: 1px solid grey;
  padding: 10px;
}

.name {
  grid-area: name;
}

.company {
  grid-area: company;
}

.color {
  grid-area: color;
}

.phoneNumber {
  grid-area: phoneNumber;
}

.job {
  grid-area: job;
}

.email {
  grid-area: email;
}

.message {
  grid-area: message;
}

.file {
  grid-area: file;
}

.delete {
  grid-area: delete;
}
