.button {
  font-weight: bold;
  border: 0;
  border-radius: 6px;
  background: #e0e0e0;
  box-shadow: 6px 6px 11px #bebebe, -6px -6px 11px #ffffff;
  padding: 0.5em 1em;
}

.button:hover {
  opacity: 0.8;
  cursor: pointer;
}

.button:active {
  border-radius: 6px;
  background: linear-gradient(145deg, #cacaca, #f0f0f0);
  box-shadow: 6px 6px 11px #bebebe, -6px -6px 11px #ffffff;
}
