.container {
    position: absolute;
    top: calc(50% - 12rem);
    left: calc(50% - 12rem);
    border-radius: 25px;
    background: linear-gradient(145deg, #e6e6e6, #ffffff);
    box-shadow:  9px 9px 18px #d9d9d9,
                -9px -9px 18px #ffffff;
    padding: 35px 24px 18px 24px;
    text-align: center;
}

.input {
    width: 5rem;
    cursor: pointer;
    border-radius: 50px;
    background: linear-gradient(145deg, #e6e6e6, #ffffff);
    box-shadow:  10px 10px 19px #bebebe,
                -10px -10px 19px #ffffff;
    margin: 0px 14px;
    padding: 11px;
}

.buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px;
}